.partnership {
  background-color: #f2f2f2;
}

.partnership__wrapper {
  @media (min-width: @desktop-width) {
    position: relative;
    height: 0;
    padding: 0;
  }
}

.partnership__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 26px 0;

  @media (min-width: @tablet-width) {
    padding: 60px 40px;
  }

  @media (min-width: @desktop-width) {
    width: 565px;
    padding: 60px 78px 60px 80px;
    position: absolute;
    z-index: 1;
    top: 95px;
    left: 0;
    background-color: #ffffff;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
}

.partnership__title {
  margin: 0;
  color: #111111;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 26px;
  }
}

.partnership__address {
  font-family: "Lato", sans-serif;
  font-style: normal;
  line-height: 20px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }
}

.partnership__map-container {
  position: relative;
  width: 100%;
  height: 362px;

  @media (min-width: @tablet-width) {
    height: 400px;
  }
}

.partnership__noscript {
  width: 100%;
  height: 362px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/partnership-map.png");
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/partnership-map@2x.png");
  }

  @media (min-width: @tablet-width) {
    height: 400px;
    background-image: url("../img/partnership-map-tablet.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/partnership-map-tablet@2x.png");
    }
  }

  @media (min-width: @desktop-width) {
    height: 400px;
    background-image: url("../img/partnership-map-desktop.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/partnership-map-desktop@2x.png");
    }
  }
}

.partnership__noscript-image {
  width: 100%;
  height: 100%;
}
