}
@media (min-resolution: 2dppx) {
  .partnership__noscript {
    background-image: url("../img/partnership-map@2x.png");
  }
}
@media (min-width: 768px) {
  .partnership__noscript {
    height: 400px;
    background-image: url("../img/partnership-map-tablet.png");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .partnership__noscript {
    background-image: url("../img/partnership-map-tablet@2x.png");
  }
}
@media (min-width: 1440px) {
  .partnership__noscript {
    height: 400px;
    background-image: url("../img/partnership-map-desktop.png");
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .partnership__noscript {
    background-image: url("../img/partnership-map-desktop@2x.png");
