  }
}
@media (min-width: 1440px) {
  .main__weight-container {
    width: calc(100% - 220px);
    max-width: 1220px;
    margin: 0 auto 71px auto;
    display: flex;
    flex-wrap: wrap;
    gap: 73px;
  }
}
.main__features {
  margin: 0 0 49px 0;
}
@media (min-width: 768px) {
  .main__features {
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .main__features {
    margin: 0 0 69px 0;
  }
}
.page__body {
  position: relative;
}
@media (min-width: 1440px) {
  .page__body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    background-color: #62a63a;
    height: 694px;
    width: 50%;
  }
  .page__body::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    background-image: url("../img/hero-bg-desktop.png");
    background-repeat: no-repeat;
    background-position: right 0;
    height: 694px;
    width: 720px;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page__body::after {
    background-image: url("../img/hero-bg-desktop@2x.png");
    background-size: 100% auto;
  }
}
.primary-button {
  background-color: #68b738;
}
.primary-button:hover {
  background-color: #5eaa2f;
}
.primary-button:active {
  color: rgba(255, 255, 255, 0.3);
}
.primary-button--show:hover {
  background-color: #ebebeb;
}
.primary-button--show:active {
  color: rgba(68, 68, 68, 0.3);
}
.primary-button--show:disabled {
  cursor: default;
  opacity: 0.3;
  pointer-events: none;
}
/* HEADER */
.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo__image:hover {
  opacity: 0.8;
}
.logo__image:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .logo__image {
    width: 174px;
    height: 50px;
  }
}
@media (min-width: 1440px) {
  .logo__image {
    width: 202px;
    height: 59px;
  }
}
.header {
  min-width: 320px;
  padding: 13px 0 14px 0;
}
@media (min-width: 768px) {
  .header {
    padding: 25px 0 0 0;
  }
}
@media (min-width: 1440px) {
  .header {
    padding: 55px 0 0 0;
  }
}
.header__logo {
  width: 190px;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .header__logo {
    align-items: flex-end;
    margin-left: 0;
  }
}
.burger {
  width: 24px;
  height: 24px;
  padding: 2px 0 4px 0;
  position: relative;
}
.burger:not(.burger--active)::before {
  content: "";
  position: absolute;
  top: 0;
  right: 1px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.burger--active::before {
  content: "";
  width: 2px;
  height: 24px;
  position: absolute;
  top: 0;
  left: 12px;
  background-color: #000000;
  transform: rotate(45deg);
}
.burger--active::after {
  content: "";
  width: 2px;
  height: 24px;
  position: absolute;
  top: 0;
  right: 11px;
  background-color: #000000;
  transform: rotate(-45deg);
}
.burger:hover::before,
.burger:hover::after {
  opacity: 0.3;
}
.burger:active::before,
.burger:active::after {
  opacity: 0.1;
}
.burger:disabled {
  opacity: 0.3;
  cursor: default;
  pointer-events: none;
}
.navigation {
  max-width: 1220px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
@media (min-width: 768px) {
  .navigation {
    align-items: flex-end;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .navigation {
    padding: 0;
  }
}
.navigation__list {
  margin: -200px 0 0 0;
  display: none;
  z-index: 2;
  position: relative;
  top: 212px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
    flex-wrap: wrap;
    gap: 33px;
    position: static;
    background-color: transparent;
  }
}
.navigation__list--active {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  position: absolute;
  top: 255px;
  left: 0;
}
@media (min-width: 768px) {
  .navigation__list--active {
    display: flex;
    flex-wrap: wrap;
    gap: 33px;
    min-width: 476px;
    position: static;
    background-color: transparent;
    flex-direction: row;
  }
}
.navigation__item {
  position: relative;
}
.navigation__item:first-of-type::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #e6e6e6;
}
.navigation__item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .navigation__item::before,
  .navigation__item::after {
    display: none;
  }
}
.navigation__link {
  text-decoration: none;
  color: #000000;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  display: block;
  width: 100%;
  padding: 22px 55px 24px 54px;
  position: relative;
}
.navigation__link:hover {
  opacity: 0.6;
}
.navigation__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 0;
    font-size: 18px;
    line-height: 24px;
  }
  .navigation__link--active::after {
    content: "";
    position: absolute;
    bottom: -13px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    padding: 0;
    font-size: 20px;
    line-height: normal;
  }
  .navigation__link--index {
    color: #ffffff;
  }
  .navigation__link--active-white::after {
    content: "";
    position: absolute;
    bottom: -13px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
  }
}
.navigation__button {
  margin-right: 20px;
}
@media (min-width: 768px) {
  .navigation__button {
    display: none;
  }
}
.navigation--nojs .navigation__button {
  display: none;
}
.navigation--nojs .navigation__list {
  display: block;
  width: 100%;
  margin-bottom: 184px;
  bottom: -211px;
  left: 0;
}
