/* ----INDEX---- */
.hero {
  background: linear-gradient(to top, #ffffff 180px, #62a63a 180px, #62a63a 100%);
  position: relative;
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 320px;
  height: calc(100% - 180px);
  background-image: url("../img/hero-bg.png");
  background-repeat: no-repeat;
}
